import React, { useState } from 'react'
import On from './On'
import zaef from './IMG_0439.PNG'
import normal from './IMG_0440.PNG'
import galakbash from './IMG_0441.PNG'
import qalaw1 from './IMG_0442.PNG'
import qalaw2 from './IMG_0443.PNG'
import qalaw3 from './IMG_0444.PNG'


const Test = (props)=>{
const [dreje,setdreje]=useState(0);
const [kesh,setkesh]=useState(30);
const [all,setall]=useState();
const [show2,setshow2]=useState('');

function inputrange(e){
 
  setdreje(e.target.value);
  setall(kesh / ( dreje*dreje ));
}
function inputrange2(e){
  setkesh(e.target.value);
  setall(kesh / ( dreje*dreje ));
}

const [saneus,setsane]=useState(false);
let sane;
let s=0;

function show(){
 setsane(true);
  setTimeout(()=>{
    if(all <= 18.5){
      setshow2(<div id='div1'>
       
      <h1> </h1><hr />
      <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
      <button>                10           175            15-20           8    </button> 
      </button>
      <img src={zaef} alt="" />
      </div>)
      }else if(  all >=18.5 && all <=22.9){
      setshow2(<div id='div1'>
      <h1></h1> <hr />
      <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
      <button>           3-5          </button>
      </button>
      <img src={normal} alt="" />
      </div>)
      }else if(23.0 <= all && all <=25.9  ){
        setshow2(<div id='div1'>
      <h1>    </h1><hr />
      <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
      <button>            </button>
      </button>
      <img src={galakbash} alt="" />
      </div>);
   
      }else if(26.0 <= all && all <=34.9  ){
        setshow2(<div id='div1'>
     <h1>         </h1><hr />
     <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
     <button>          1         3     10-15      </button>
     </button>
     <img src={qalaw1} alt="" />
     </div>)
    
      }else if(35.0 <= all && all <=39.0  ){
        setshow2(<div id='div1'>
      <h1>  </h1><hr />
      <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
      <button>                 2      3  30-25   </button>
      </button>
      <img src={qalaw2} alt="" />
      </div>)
    
      }else if(all > 40.0 ){
        setshow2(<div id='div1'> 
        <h1>|||   </h1><br /><hr />
        <button> <button id='neasen'><b>{props.namee.target.value}</b><b >   </b><br /></button>
        <button>        40-50     </button>
        </button>
        <img src={qalaw3} alt="" />
        </div>)
    
      }
      setsane(false)
      
  },3000)
  

}



if(saneus==true){
  sane =  <button id='icc' > <i style={{color:'white'}} class="gg-time"><h2 style={{marginLeft:'30px', }}>3s</h2></i></button>
}

  return(
<div id='all2'>
     <br /><br />
     
      <br />
      <b id='tebene'> :                </b><br /><br />
      <br /><br /><br />
      <button id='dreje'> {dreje}cm</button><br /><br />
<div id='divrange'>
  <button id='d'></button>
  <input className='range1' type="range"   max={3.0} min={1.0} step={0.01} onInput={inputrange}/>
</div>
      <br /><br /> <hr />
      
      <button id='kesh'>{kesh}kg</button><br /><br />
<div id='divrange'>
  <button id='d'></button>
<input className='range2' type="range"  max={200}  step={1} min={30} onInput={inputrange2} />
 </div>
    
    <input type="submit"  id='sub' value={'Click Here'} onClick={show}/>
   {sane}
     <br />
    <br /><br /><br /><br />
<div id='show'>
  <button id='closinfo' onClick={()=>{
    setshow2('')
    setkesh(30)
    setdreje(0)
    }}><i class="gg-close"></i></button>
  {show2}
</div>
</div>
  )
}

export default Test